<template>
    <div v-if="ready">
        <div class="row"> 
            <div class="col-12">
                <h1>{{ strings.locationsLabel }}</h1> 

                <div>{{ strings.line1 }}
                    <br>{{ strings.line2 }}
                    <br>{{ strings.line3 }}
                    <br>{{ strings.line4 }}
                    <br>{{ strings.gpsLabel }}:   23°35’16.6″N 58°21’59.1″E 
                </div>
            </div>
            <div class="col-12">
                <div class="py-5">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d935314.3693802478!2d58.22855719537428!3d23.69126798053025!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sus!4v1551152956897" width="100%" height="450" frameborder="0" style="border:0" allowfullscreen></iframe>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'location',
  props: [ 'lang' ],
  data() {
    return{
      url: this.$parent.url,
      ready: false,
      strings: {}
    }
  },
  watch:{
    lang: function(newLang, oldLang){
      this.setLang();
    }
  },
  created: function(){
    this.setLang();
  },
  methods: {
    setLang : function(){
      axios.get(this.url+'/assets/lang/'+this.lang+'/location.json')
      .then(r=> {
        this.strings = r.data;
        this.ready = true;
      })
    }
  }
}
</script>
